<template>
  <div class="label">
    <div class="main m">
      <div class="main-left">
        <div class="head">
          <!--<div class="img">-->
          <!--<img src="https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=873265023,1618187578&fm=27&gp=0.jpg"-->
          <!--alt="">-->
          <!--</div>-->
          <div class="title">{{name}}</div>
        </div>
        <!--<detail :article="labels"></detail>-->
        <articles :article="labels"  v-on:islike="islike" v-on:isrubbish="isrubbish" ></articles>
      </div>
      <div class="main-right">
        <!--<tag></tag>-->
        <recommend></recommend>
      </div>
    </div>
  </div>
</template>

<script>

  export default {
    name: "labels",
    data() {
      return {
        name: '',
        labels: []
      }
    },
    mounted() {
      this.name = this.$route.query.label;
      this.getlabel()
    },
    methods: {
      //点赞成功
      islike(data){
        if (data == "true") {
          this.getlabel();
        }
      },
      //踩成功
      isrubbish(data){
        if (data == "true") {
          this.getlabel();
        }
      },


      getlabel() {
        this.$post('index/data/sos_label', {like: this.name}).then(res => {
          // this.labels=res.data;
          this.labels = res.data.data;
          this.labels.map((value, index, array) => {
            value.head = this.$httpUrl + "/uc_server/avatar.php?uid=" + value.uid + "&amp;size=big"
          });
          this.labels.slice();
        }).catch(err => {
          console.log(err);
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  $main: #409eff !default;
  .label {

    .main {
      display: flex;
      justify-content: space-between;
    }

    .main-left {
      width: 790px;

      .head {
        width: 100%;
        height: 170px;
        margin-bottom: 30px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid rgba(221, 221, 221, 1);

        .img {
          width: 70px;
          height: 70px;
          border-radius: 6px;
          overflow: hidden;

          img {
            width: 100%;
            height: 100%;
          }
        }

        .title {
          margin-left: 27px;
          height: 19px;
          font-size: 20px;
          font-weight: 400;
          color: rgba(51, 51, 51, 1);
          line-height: 18px;
        }
      }
    }

    .main-right {
      width: 360px;
    }
  }
</style>



